.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

._loading-overlay{
	height:85%;
}
#messages{
	height:100%;
	background-color: grey;
	overflow-y:scroll;
}

.message{
	border-radius:25px;
	background:#73AD21;
	padding:10px;
	width: 70%;
	margin-bottom:5px;
	float:right;
	word-break:break-all;
}

#header{
	height:10%;
	background-color:green;
}

.author{
	text-decoration:underline;
	color:darkblue;
}

.my-message{
	float:left;
	background:lightcoral;
}

html,body, #root, #root>div, #pageDiv{
	height:100%;
	overflow-y:hidden;
	overflow-x:hidden;
}

#footerDiv{
	height:3%;
}

.bodyDiv,#roomWrapper{
	height:97%
}


